<template>
  <section class="msite">
    <!--首页头部-->
    <NavHeader :title="address.name || '正在请求中...'">
      <span class="header_search" slot="left" @click="$router.replace('/search')">
        <i class="iconfont icon-sousuo"></i>
      </span>
      <span class="header_login" slot="right">
        <span class="header_login_text" v-if="!user._id" @click="$router.push('/login')">登录|注册</span>
        <i class="iconfont icon-person" v-else @click="$router.push('/userinfo')"></i>
      </span>
    </NavHeader>
    <!--首页导航-->
    <nav class="msite_nav">
      <div class="swiper-container" v-if="categorys.length>0">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(categorys,index) in categorysArr" :key="index">
            <a href="javascript:" class="link_to_food" v-for="(c,index) in categorys" :key="index">
              <div class="food_container">
                <img :src="'https://fuss10.elemecdn.com' + c.image_url">
              </div>
              <span>{{c.title}}</span>
            </a>
          </div>
        </div>
        <!-- Add Pagination -->
        <div class="swiper-pagination"></div>
      </div>
      <!--loading图-->
      <img src="./images/msite_back.svg" alt="back" v-else>
    </nav>
    <!--首页附近商家-->
    <div class="msite_shop_list">
      <div class="shop_header">
        <i class="iconfont icon-xuanxiang"></i>
        <span class="shop_header_title">附近商家</span>
      </div>
      <ShopList/>
    </div>
  </section>
</template>

<script>
  import {mapState} from 'vuex';
  import ShopList from '../../components/ShopList/ShopList'
  import Swiper from 'swiper';
  import 'swiper/dist/css/swiper.css';
  export default {
    mounted () {
      this.$store.dispatch('getShops');
      this.$store.dispatch('getFoods');
    },
    computed: {
      ...mapState({
        address: state => state.mySite.address,
        categorys: state => state.mySite.categorys,
        user: state => state.user.user
      }),
      categorysArr () {
        const {categorys} = this;
        // 二维数组
        const bigArr = [];
        //一维数组
        let smallArr = [];
        categorys.forEach(c => {
          // 将小数组保存到大数组(每个小数组只需要添加一次)
          if (smallArr.length === 0) {
            bigArr.push(smallArr);
          }
          smallArr.push(c);
          if (smallArr.length === 8) {
            smallArr = [];
          }
        })
        return bigArr;
      },
    },
    components: {
      ShopList,
    },
    watch: {
      categorys () {
        // 将回调延迟到下次 DOM 更新循环之后执行。在修改数据之后立即使用它，然后等待 DOM 更新
        this.$nextTick(()=>{
          //创建swiper时机，列表数据显示之后。
          new Swiper('.swiper-container',{
            loop: true,
            // 如果需要分页器
            pagination: {
              el: '.swiper-pagination',
            }
          })
        })
      },
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/mixins.styl"
  .msite  //首页
    width 100%
    .msite_nav
      bottom-border-1px(#e4e4e4)
      margin-top 45px
      height 200px
      background #fff
      .swiper-container
        width 100%
        height 100%
        .swiper-wrapper
          width 100%
          height 100%
          .swiper-slide
            display flex
            justify-content center
            align-items flex-start
            flex-wrap wrap
            .link_to_food
              width 25%
              .food_container
                display block
                width 100%
                text-align center
                padding-bottom 10px
                font-size 0
                img
                  display inline-block
                  width 50px
                  height 50px
              span
                display block
                width 100%
                text-align center
                font-size 13px
                color #666
        .swiper-pagination
          >span.swiper-pagination-bullet-active
            background #02a774
    .msite_shop_list
      top-border-1px(#e4e4e4)
      margin-top 10px
      background #fff
      .shop_header
        padding 10px 10px 0
        .shop_icon
          margin-left 5px
          color #999
        .shop_header_title
          color #999
          font-size 14px
          line-height 20px
</style>



